<template>
    <div>
        <ConsoleHeader />
      
        <div class="content-wrapper">
            <!-- Content Header (Page header) -->
            <div class="content-header">
                <div class="container-fluid">
                    <div class="row mb-2">
                    <div class="col-sm-6 text-start">
                        <h1 class="m-0 text-dark">Units<sup><a href="javascript:void(0)" class="text-dark" @click.prevent="newUnitView" title="Create"><i class="mdi mdi-plus-circle"></i></a></sup></h1>
                    </div><!-- /.col -->
                    <div class="col-sm-6">
                        <ol class="breadcrumb float-sm-right">
                        <li class="breadcrumb-item"><router-link to="/console" >Home</router-link></li>
                        <li class="breadcrumb-item active">Units</li>
                        </ol>
                    </div><!-- /.col -->
                    </div><!-- /.row -->
                </div><!-- /.container-fluid -->
            </div>
            <!-- /.content-header -->

            <!-- Main content -->
            <section class="content">
                <div class="container-fluid" >
                    
                    <div class="pb-5" v-if="NewUnitView === false">
                        
                        <div v-if="units.length < 1" class="text-start">
                            <b>No Units added yet!</b>
                        </div>

                        <div class="row no-gutters pb-5" v-else >
                            <div class="col-xl-3 col-sm-6 col-6 h-100" v-for="unit in units" v-bind:key="unit.id">
                                <!-- small box -->
                                <div class="small-box bg-dark h-100 pt-4">
                                    <div class="inner">
                                        <div v-if="unit.image === null">
                                            <img src="@/assets/images/image.png" class="img-fluid" style="width: 8em;" />
                                        </div>
                                        <div v-else >
                                            <img :src="unit.image" class="img-fluid" style="width: 7em; height: 7em; object-fit: contain;" />
                                        </div>

                                        <h5 class="text-truncate" :title="unit.name">{{unit.name}}</h5>
                                    </div>
                                   
                                    <div class="small-box-footer mb-0 p-2">
                                        <a href="javascript:void(0)" class="text-success mr-2" @click="filterUnitDetails(unit.id)" >More</a> 
                                        <a href="javascript:void(0)" class="text-light" @click="filterUnit(unit.id)" >Edit</a> 
                                        <a href="javascript:void(0)" class="p-2 text-danger" @click="handleDeleteUnit(unit.id)">Delete</a>
                                    </div>
                                </div>
                                

                            </div>
                        </div>


                    </div>

                    <!-- Create Unit -->
                    <div v-else >
                        <div v-if="Unit === null">
                            <form method="POST" @submit.prevent="handleNewUnit">
                                <h3 class="text-start">
                                    <a href="javascript:void(0)" class="text-dark" @click="loadUnitView"><i class="mdi mdi-chevron-left"></i></a> 
                                    Create a new unit
                                </h3>
                                <div class="pb-5">
                
                                    <div class="col-xl-6 col-md-12 my-4">
                                        <!-- XHR Response -->
                                        <!-- <div class="form-group text-start">
                                            <input type="file" name="file_holder" ref="image" class="d-none" id="profile_image" @change.prevent="changeImage" />
                                            <label for="profile_image" style="cursor: pointer;"><img src="@/assets/images/profile.png" class="img-fluid" style="width: 5em;"/></label>
                                        </div> -->
                                            
                                        <div class="form-group text-start">
                                            <label for="">Name</label>
                                            <input type="text" v-model="form.name" class="form-control remove-radius" placeholder="Enter name" required/>
                                        </div>
                                            
                                        <div class="form-group text-start">
                                            <label for="">Pathname(optional)</label>
                                            <input type="text" v-model="form.pathname" class="form-control remove-radius" placeholder="Enter pathname" />
                                        </div>
                                            
                                        <div class="form-group text-start">
                                            <label for="">Slug</label>
                                            <input type="text" v-model="form.slug" class="form-control remove-radius" placeholder="Enter slug" required />
                                        </div>
                                            
                                        <div class="form-group text-start">
                                            <label for="">Description(optional)</label>
                                            <textarea v-model="form.description" class="form-control remove-radius" placeholder="Enter description"></textarea>
                                        </div>
                
                                    </div>

                                    <div class="col-xl-6 col-md-12 mb-2 text-start">
                                        <button class="btn btn-success" type="submit"><i class="fa fa-save"></i> Save</button>
                                    </div>

                                </div>

                            </form>
                        </div>

                        <div v-else >
                            <form method="POST" @submit.prevent="handleUpdatedUnit">
                                <h3 class="text-start">
                                    <a href="javascript:void(0)" class="text-dark" @click="loadUnitView"><i class="mdi mdi-chevron-left"></i></a> 
                                    Update unit
                                </h3>
                                <div class="pb-5">
                
                                    <div class="col-xl-6 col-md-12 my-4">
                                        <!-- XHR Response -->
                                        <div class="form-group text-start">
                                            <input type="file" name="file_holder" ref="image" class="d-none" id="profile_image" @change.prevent="changeImage" />
                                            <label for="profile_image" style="cursor: pointer;" v-if="Unit.image === null" ><img src="@/assets/images/image.png" class="img-fluid" style="width: 5em;" /></label>
                                            <label for="profile_image" style="cursor: pointer;" v-else ><img :src="Unit.image" class="img-fluid" style="width: 5em;"/></label>
                                        </div>
                                   
                                        <div class="form-group text-start">
                                            <label for="">Name</label>
                                            <input type="text" v-model="Unit.name" class="form-control remove-radius" placeholder="Enter name" required/>
                                        </div>
                                            
                                        <div class="form-group text-start">
                                            <label for="">Pathname(optional)</label>
                                            <input type="text" v-model="Unit.pathname" class="form-control remove-radius" placeholder="Enter pathname" />
                                        </div>
                                            
                                        <div class="form-group text-start">
                                            <label for="">Slug</label>
                                            <input type="text" v-model="Unit.slug" class="form-control remove-radius" placeholder="Enter slug" required />
                                        </div>
                                            
                                        <div class="form-group text-start">
                                            <label for="">Description(optional)</label>
                                            <textarea v-model="Unit.description" class="form-control remove-radius" placeholder="Enter description"></textarea>
                                        </div>

                
                                    </div>

                                    <div class="col-xl-6 col-md-12 mb-2 text-start">
                                        <button class="btn btn-success" type="submit"><i class="fa fa-save"></i> Save</button>
                                    </div>

                                </div>

                            </form>
                        </div>

                    </div>

                </div>

            </section>
            <!-- /.content -->
        </div>

        <ConsoleFooter />
    </div>
</template>


<script>
import ConsoleHeader from "./layouts/ConsoleHeader.vue";
import ConsoleFooter from "./layouts/ConsoleFooter.vue";

import { mapActions, mapGetters } from 'vuex';

import axios from 'axios';
import AuthenticationService from '@/services/AuthenticationService';


export default {
    name: "ConsoleUnits",
    data(){
        return {
            NewUnitView: false,
            Unit: null,
            form: {
                name: '',
                pathname: '',
                slug: '',
                description: ''
            }
        }
    },
    computed: mapGetters(['units']),
    components: {
        ConsoleHeader,
        ConsoleFooter
    },
    methods: {
        ...mapActions(['fetchUnits', 'deleteUnit', 'createUnit', 'updateUnit']),
        async handleNewUnit(){
            await this.createUnit(this.form);
            this.NewUnitView = false;
            
        },
        async newUnitView(){
            this.NewUnitView = true;
            this.Unit = null;
        },
        async loadUnitView(){
            this.NewUnitView = false;
            this.Unit = null;
        },
        async filterUnit(id){
            this.Unit = this.units.filter(unit => unit.id === id);
            if (this.Unit.length > 0) {
                this.Unit = this.Unit[0];
                this.NewUnitView = null;
            }
        },
        async filterUnitDetails(id){
            await this.$store.dispatch('fetchUnitDetails', id);
            this.$router.push('/console/executives');
        },
        async changeImage(){
            this.image = this.$refs.image.files[0];
            let formData = new FormData();
            formData.append('upload_preset', 'offices');
            formData.append('tags', 'unsigned upload, offices');
            formData.append('file', this.image);
            

            let response = await axios.post(`https://api.cloudinary.com/v1_1/cacsafpi/upload`, formData);

            if (response.data.secure_url !== null) {
                this.Unit.image = response.data.secure_url;
                let old_image = this.Unit.image;
                await this.updateUnit({id: this.Unit.id, unit: this.Unit});

                if (old_image !== null) {
                    await AuthenticationService.removeImage({old_image});
                }

            }
        },
        async handleDeleteUnit(id){
            let unit = this.units.filter(unit => unit.id === id);
            if (unit.length > 0) {
                await this.deleteUnit(id);
            }
            
        },
        async handleUpdatedUnit(){
            if (this.Unit !== null) {
                await this.updateUnit({id: this.Unit.id, unit: this.Unit});
                // await this.loadUnitView();
            }
        }
    },
    created(){
        this.fetchUnits();
    }

}
</script>

<style scoped>
    .small-box{
        margin-right: 1em;
    }
</style>